import { message } from 'antd';
import {
  MDBBtn, MDBInput,
  MDBModal,
  MDBModalBody,
  MDBModalHeader
} from 'mdbreact';
import { inject, observer } from 'mobx-react';
import React, { Component } from 'react';

class EditCat extends Component {
  state = {
    modal: false,
  };

  toggle = () => () => {
    
    this.setState({
      modal: !this.state.modal,
    });
  };

  getUniqueID = () => {
    let uniqueID = '';
    for (var i = 0; i < 1; i++) {
      uniqueID += Date.now() + (Math.random() * 1).toFixed();
    }

    return uniqueID;
  };
  submitHandler = (event) => {
    event.preventDefault();
    event.target.className += ' was-validated';

    let {
      startingStore: {editPrintOption, printoption},
    } = this.props;
    printoption.setProperty('optionID', this.props.data.optionID);
    printoption.setProperty('printCat', this.props.data.printCat);
    const success = () => {
      message
        .loading('', 1)
        .then(() => message.success('Changes saved', 1));
    };

    setTimeout(() => {
      success();
    }, 100);

    this.setState({
      modal: !this.state.modal,
    });
    editPrintOption();
  };

  changeHandler = (event) => {
    this.setState({[event.target.name]: event.target.value});
  };

  render() {
    let {
      startingStore: {printoption},
    } = this.props;

   
    return (
      <div>
        <MDBBtn onClick={this.toggle()} color='transparent'>
          Edit
        </MDBBtn>
        {this.props.data.printCat === 'material' ? (    
        <MDBModal isOpen={this.state.modal} toggle={this.toggle()} centered>
          <MDBModalHeader toggle={this.toggle()} style={{backgroundColor:'#231F20'}}>
           <span style={{color:'white'}}> Edit Material</span>
          </MDBModalHeader>
          <MDBModalBody className='adminmodalbody'>
            <form onSubmit={this.submitHandler} className='formbtn'>
              <MDBInput
                label='Material Name'
                type='text'
                valueDefault={this.props.data.printOptions}
              
                onChange={(printOptions) =>
                  printoption.setProperty('printOptions', printOptions.target.value)
                }
                required
              >
                <div className='invalid-feedback'>
                  Please provide a valid Category Name.
                </div>
              </MDBInput>
              <MDBBtn className='submitreg clearfix' type='submit'>
                SAVE
              </MDBBtn>
            </form>
          </MDBModalBody>
        </MDBModal>):(   
           <MDBModal isOpen={this.state.modal} toggle={this.toggle()} centered>
        <MDBModalHeader toggle={this.toggle()} style={{backgroundColor:'#231F20'}}>
           <span style={{color:'white'}}> Edit Frame</span>
          </MDBModalHeader>
          <MDBModalBody className='adminmodalbody'>
            <form onSubmit={this.submitHandler} className='formbtn'>
              <MDBInput
                label='Frame Name'
                type='text'
                valueDefault={this.props.data.printOptions}
              
                onChange={(printOptions) =>
                  printoption.setProperty('printOptions', printOptions.target.value)
                }
                required
              >
                <div className='invalid-feedback'>
                  Please provide a valid Category Name.
                </div>
              </MDBInput>
              <MDBBtn className='submitreg clearfix' type='submit'>
                SAVE
              </MDBBtn>
            </form>
          </MDBModalBody>
        </MDBModal>)}
    
      </div>
    );
  }
}

export default inject('startingStore')(observer(EditCat));
