import {
  MDBCard,
  MDBCardBody,




  MDBCol, MDBRow, MDBTable,
  MDBTableBody,
  MDBTableHead
} from 'mdbreact';
import { inject, observer } from 'mobx-react';
import React, { Component } from 'react';
import EditCat from './EditCat';
import EditPrintSize from './EditPrintSize';
import EditStyle from './EditStyle';

class CategoryTable extends Component {
  componentDidMount() {
    let {
      startingStore: {getCategories, getStyles,getPrintSize},
    } = this.props;
    getCategories();
    getStyles();
    getPrintSize();
  }

  render() {
    let {
      startingStore: {listOfCategories, listOfStyles,listOfPrintSize},
    } = this.props;
  
    return (
      <div className='clearfix'>
        <MDBRow className='mb-4 row50'>
          <MDBCol md='12'>
            <MDBCard>
              <MDBCardBody>
                <h3>Theme List</h3>
                <MDBTable hover className='tablescroll'>
                  <MDBTableHead color='blue-grey lighten-4'>
                    <tr>
                      <th>No.</th>
                      <th>Theme Name</th>
                      <th>Action</th>
                    </tr>
                  </MDBTableHead>
                  <MDBTableBody>
                    {listOfCategories.map((data,index) => {
                      let no = index +1;
                      return(
                      <tr key={index}>
                        <td>{no}</td>
                        <td>{data.catType}</td>
                        <td className='edittd'>
                          <EditCat data={data} />
                        </td>
                      </tr>
                    )})}
                  </MDBTableBody>
                </MDBTable>
              </MDBCardBody>
            </MDBCard>
          </MDBCol>
        </MDBRow>

        <MDBRow className='mb-4 row50'>
          <MDBCol md='12'>
            <MDBCard>
              <MDBCardBody>
                <h3>Style List</h3>
                <MDBTable hover className='tablescroll'>
                  <MDBTableHead color='blue-grey lighten-4'>
                    <tr>
                      <th>No.</th>
                      <th>Style Name</th>
                      <th>Action</th>
                    </tr>
                  </MDBTableHead>
                  <MDBTableBody>
                    {listOfStyles.map((data,index) => {
                      let no = index +1;
                      return(
                      <tr key ={index}>
                        <td>{no}</td>
                        <td>{data.styleType}</td>
                        <td className='edittd'>
                          <EditStyle data={data} />
                        </td>
                      </tr>
                    )})}
                  </MDBTableBody>
                </MDBTable>
              </MDBCardBody>
            </MDBCard>
          </MDBCol>
        </MDBRow>



        <MDBRow className='mb-4 row50'>
          <MDBCol md='12'>
            <MDBCard>
              <MDBCardBody>
                <h3>Printing Size</h3>
                <MDBTable hover className='tablescroll'>
                  <MDBTableHead color='blue-grey lighten-4'>
                    <tr>
                      <th>No.</th>
                      <th>Size</th>
                      <th>Action</th>
                    </tr>
                  </MDBTableHead>
                  <MDBTableBody>
                    {listOfPrintSize.map((data,index) => {

                        let no = index +1;
return(
                      <tr key ={index}>
                        <td>{no}</td>
                        <td>{data.printSize}</td>
                        <td className='edittd'>
                          <EditPrintSize data={data} />
                        </td>
                      </tr>
)  })}
                  </MDBTableBody>
                </MDBTable>
              </MDBCardBody>
            </MDBCard>
          </MDBCol>
        </MDBRow>

      </div>
    );
  }
}

export default inject('startingStore')(observer(CategoryTable));
