import { message } from 'antd';
import {
  MDBBtn, MDBContainer,




  MDBIcon, MDBInput,
  MDBModal,
  MDBModalBody,
  MDBModalHeader
} from 'mdbreact';
import { inject, observer } from 'mobx-react';
import React, { Component } from 'react';

class AddCat extends Component {
  state = {
    modal: false,
   
  };

  toggle =()=> ()  => {
    
    this.setState({
      modal: !this.state.modal,
    });
  };

  getUniqueID = () => {
    let uniqueID = '';
    for (var i = 0; i < 1; i++) {
      uniqueID += Date.now() + (Math.random() * 1).toFixed();
    }

    return uniqueID;
  };
  submitHandler = (event) => {
    event.preventDefault();
    event.target.className += ' was-validated';

    let {
      startingStore: {addPrintOption , printoption},
    } = this.props;
    printoption.setProperty('optionID', this.getUniqueID());
    printoption.setProperty('printCat', 'material');
    const success = () => {
      message
        .loading('', 1.2)
        .then(() => message.success('Successfully added', 1));
    };

    setTimeout(() => {
      success();
    }, 1000);
    addPrintOption ();
  };






  changeHandler = (event) => {
    this.setState({[event.target.name]: event.target.value});
  };



  render() {
    let {
      startingStore: {printoption,},
    } = this.props;
    return (
      <MDBContainer>
        <div style={{float: 'left'}}>
          <MDBBtn onClick={this.toggle()} color='transparent'>
            <MDBIcon icon='plus-circle' size='2x' />
            Add Material
          </MDBBtn>
          <MDBModal isOpen={this.state.modal} toggle={this.toggle()} centered>
            <MDBModalHeader toggle={this.toggle()}>
              Add Material
            </MDBModalHeader>
            <MDBModalBody className='adminmodalbody'>
              <form onSubmit={this.submitHandler} className='formbtn'>
                <MDBInput
                  label='Matherial Name'
                  type='text'
                  onChange={(printOptions) =>
                    printoption.setProperty('printOptions', printOptions.target.value)
                  }
                  required
                >
                  <div className='invalid-feedback'>
                    Please provide a valid email.
                  </div>
                </MDBInput>
                <MDBBtn className='submitreg clearfix' type='submit'>
                  SUBMIT
                </MDBBtn>
              </form>
            </MDBModalBody>
          </MDBModal>
        </div>
 
      </MDBContainer>
    );
  }
}

export default inject('startingStore')(observer(AddCat));
