import { message } from 'antd';
import { MDBBtn, MDBInput } from 'mdbreact';
import { inject, observer } from 'mobx-react';
import moment from 'moment';
import React, { Component } from 'react';
import Footer from '../../components/Footer';
import Navbar from '../../components/Navbar';
import FBReg from '../../components/RegisterFB/';
import GmailReg from './../../components/RegisterGmail/';

 class ArtGalReg extends Component {

  state ={
   
    password:'',
    confPassword:'',
   

  }


  handleChange = input => e =>{
    this.setState({[input]:e.target.value});

}

  submitHandler = (event) => {



    event.preventDefault();
   

    let {
      startingStore: {listOfUsers,addAccount, account},
    } = this.props;


    let filAccounts = listOfUsers.filter(usr => usr.accContact === account.accContact && account.acc_Status === 'Active' || usr.accEmailAddress === account.accEmailAddress && account.acc_Status === 'Active').length


    if (filAccounts === 0){

    account.setProperty('accessType', 'Standard')
    account.setProperty('acc_Status', 'Active')
    account.setProperty('dateAdded', moment().format('MMM/DD/YYYY'))
    account.setProperty('accID',  `${moment().format('MDYY')}-${ Math.floor(100 + Math.random() * 900)}`)
    account.setProperty('password',this.state.password)
    account.setProperty('regType','LocalAccount')
    if (this.state.password === this.state.confPassword){
      event.target.className += 'was-validated';
    addAccount();

    const success = () => {
      // message.then(() =>
      message.success('You have been successfully registered', 2);
      // );
    };
    setTimeout(() => {
      success();
    
    }, 500);

    setTimeout(() => {
      this.props.history.push('/');
    }, 3000);
  }else{
    const error = () => {
      // message.then(() =>
      message.error('Please check your password', 2);
      // );
    };
    setTimeout(() => {
      error();
    
    }, 500);
  }

}else{
  const error = () => {

  message.error('Your email or contact number is already taken', 2);

};
setTimeout(() => {
  error();

}, 500);
}




  };

  changeHandler = (event) => {
    this.setState({[event.target.name]: event.target.value});
  };

  render() {
    let {
      startingStore: {account},
    } = this.props;

    return (
      <div className='home'>
        <Navbar />
        <div className='maincon con'>
          <div className='reg clearfix'>
            <div className='left'>
              <h2>
                Art Enthusiast <br /> Registration
              </h2>
              <p>Fill up the form and start exploring the best artworks!</p>
            </div>
            <form onSubmit={this.submitHandler} className='regform formbtn'>
         
        
              <MDBInput
                label='First Name'
                type='text'
                style={{textTransform: 'capitalize'}}
                onChange={(accFname) =>
                  account.setProperty('accFname', accFname.target.value)
                }
                required
              >
                <div className='invalid-feedback'>
                  Please provide a First Name.
                </div>
              </MDBInput>
              <MDBInput
                label='Last Name'
                style={{textTransform: 'capitalize'}}
                type='text'
                onChange={(accLname) =>
                  account.setProperty('accLname', accLname.target.value)
                }
                required
              >
                <div className='invalid-feedback'>
                  Please provide a Last Name.
                </div>
              </MDBInput>
              <MDBInput
                label='Birth Year'
                type='number'
                onChange={(birthYear) =>
                  account.setProperty('birthYear', birthYear.target.value)
                }
                required
              >
                <div className='invalid-feedback'>
                  Please provide a Birth Year.
                </div>
              </MDBInput>
              <MDBInput
                label='Contact Number'
                type='text'
                maxlength='11'
                onChange={(accContact) =>
                  account.setProperty('accContact', accContact.target.value)
                }
                required
              >
                <div className='invalid-feedback'>
                  Please provide a Contact Number.
                </div>
              </MDBInput>
  
              <MDBInput
                label='Home Address'
                type='text'
                style={{textTransform: 'capitalize'}}
                onChange={(accAddress) =>
                  account.setProperty('accAddress', accAddress.target.value)
                }
                required
              >
                <div className='invalid-feedback'>
                  Please provide a Address.
                </div>
              </MDBInput>

              <MDBInput
                label='Email Address'
                type='email'
                onChange={(accEmailAddress) =>
                  account.setProperty(
                    'accEmailAddress',
                    accEmailAddress.target.value
                  )
                }
                required
              >
                <div className='invalid-feedback'>
                  Please provide a valid email.
                </div>
              </MDBInput>
              <MDBInput
                label='Password'
                type='password'
                // onChange={(password) =>
                //   account.setProperty('password', password.target.value)
                // }
                onChange={this.handleChange('password')}
                required
              >
                <div className='invalid-feedback'>
                  Please provide a valid password.
                </div>
              </MDBInput>
              <MDBInput
                label='Confirm Password'
                type='password'
                // onChange={(password) =>
                //   account.setProperty('password', password.target.value)
                // }
                onChange={this.handleChange('confPassword')}
                required
              >
                <div className='invalid-feedback'>
                  Please provide a valid password.
                </div>
              </MDBInput>

          
              <FBReg user='Standard'/>
              <GmailReg user='Standard'/>
              <MDBBtn
                className='submitreg clearfix'
                type='submit'
                color='#FAE933'
              >
                Register
              </MDBBtn>
            </form>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}

export default inject('startingStore')(observer(ArtGalReg));
