import React, { Component, Fragment } from "react";
import {
  MDBContainer,
  MDBBtn,
  MDBModal,
  MDBModalBody,
  MDBModalHeader,
  MDBModalFooter,
  MDBLink,
  MDBInput,
  
} from "mdbreact";
import { message } from 'antd';
import './../../css/style.css';
import './../../css/normalize.css';
import './../../css/printart.css';
import 'react-toastify/dist/ReactToastify.css';

class ModalPage extends Component {
  state = {
    modal: false,
  };



  render() {


   let toggle = () => {
   
      this.setState({
        modal: !this.state.modal,
      });
    };
  
   let toggleSubmit = ()=>{

    const success = () => {
      message
        .loading('', 1)
        .then(() => message.success('Equiry submitted', 1));
    };

    setTimeout(() => {
      success();
    }, 800);


    setTimeout (()=>{
      this.setState({
        modal: !this.state.modal,
      });
    },300)
    
    }

    return (
      <Fragment>
        <MDBLink to="#" color="primary" onClick={toggle}>
          We can help
        </MDBLink>
        <MDBContainer>
          <MDBModal
            isOpen={this.state.modal}
            toggle={toggle}
            centered
            size="sm"
          >
            <MDBModalHeader toggle={toggle} style={{backgroundColor:'#FAE933'}}>
              How can we help you?
            </MDBModalHeader>
            <MDBModalBody>
              <form className="regform formbtn">
                <MDBInput label="Name" type="text" required>
                  <div className="invalid-feedback">Please provide your Name.</div>
                </MDBInput>
                <MDBInput label="Contact#" type="number" required>
                  <div className="invalid-feedback">
                    Please provide your contact number.
                  </div>
                </MDBInput>
                <MDBInput label="Email" type="email" required>
                  <div className="invalid-feedback">
                    Please provide your email.
                  </div>
                </MDBInput>
                <MDBInput label="Company" type="text" >
                  <div className="invalid-feedback">
                    Please provide your company.
                  </div>
                </MDBInput>

                <div >
                   
                    <select required>
                      <option selected value=''>
                      I'm a/an 
                      </option>
                      <option value='Art Collector'>Art Collector</option>
                      <option value='Art Gallery Owner/Representative'>Art Gallery Owner/Representative</option>
                      <option value='Contractor'>Contractor</option>
                      <option value='Curator'>Curator</option>
                      <option value='Hotel Owner/Representative'>Hotel Owner/Representative</option>
                      <option value='Interior Designer/Decorator'>Interior Designer/Decorator</option>
                    </select>
                    
                  </div>

                  <div className="form-group">
            <span htmlFor="myinquiry" style={{float:'left'}}>
            Inquiry:
            </span>
            <textarea
            className="form-control"
            id="myinquiry"
            rows="5"
            required
            />
        </div>

              </form>
            </MDBModalBody>
            <MDBModalFooter>
              <button  className="btn btnBlack" onClick={toggle} size="sm">
                Close
              </button>
              <button className="btn btnYellow"size="sm" onClick={toggleSubmit}>
                Submit
              </button>
            </MDBModalFooter>
          </MDBModal>
        </MDBContainer>
      </Fragment>
    );
  }
}

export default ModalPage;
