import {decorate, observable} from 'mobx';
import Model from './Model';

class PrintOptions extends Model {
  constructor(props) {
    const defaults = {
      optionID: ' ',
      printCat:'',
      printOptions: ' ',
    };
    super({...defaults, ...props});
  }

}

decorate(PrintOptions, {
  optionID: observable,
  printCat:observable,
  printOptions: observable,
});

export default PrintOptions;