import {
  MDBCard,
  MDBCardBody,




  MDBCol, MDBRow, MDBTable,
  MDBTableBody,
  MDBTableHead
} from 'mdbreact';
import { inject, observer } from 'mobx-react';
import React, { Component } from 'react';
import EditMat from '../EditOpt'
import EditFrame from '../EditOpt'

class CategoryTable extends Component {
  componentDidMount() {
    let {
      startingStore: {getPrintOption},
    } = this.props;
    getPrintOption();

  }

  render() {
    let {
      startingStore: {listOfPrintoption},
    } = this.props;
  
    return (
      <div className='clearfix'>
        <MDBRow className='mb-4 row50'>
          <MDBCol md='12'>
            <MDBCard>
              <MDBCardBody>
                <h3>Material Lists</h3>
                <MDBTable hover className='tablescroll'>
                  <MDBTableHead color='blue-grey lighten-4'>
                    <tr>
                      <th>No.</th>
                      <th>Theme Name</th>
                      <th>Action</th>
                    </tr>
                  </MDBTableHead>
                  <MDBTableBody>
                    {listOfPrintoption.filter(data => data.printCat === 'material').map((data,index) => {
                      let no = index +1;
                      return(
                      <tr key={index}>
                        <td>{no}</td>
                        <td>{data.printOptions}</td>
                        <td className='edittd'>
                          <EditMat data={data} />
                        </td>
                      </tr>
                    )})}
                  </MDBTableBody>
                </MDBTable>
              </MDBCardBody>
            </MDBCard>
          </MDBCol>
        </MDBRow>

        <MDBRow className='mb-4 row50'>
          <MDBCol md='12'>
            <MDBCard>
              <MDBCardBody>
                <h3>Frame Lists</h3>
                <MDBTable hover className='tablescroll'>
                  <MDBTableHead color='blue-grey lighten-4'>
                    <tr>
                      <th>No.</th>
                      <th>Style Name</th>
                      <th>Action</th>
                    </tr>
                  </MDBTableHead>
                  <MDBTableBody>
                    {listOfPrintoption.filter(data => data.printCat === 'frame').map((data,index) => {
                      let no = index +1;
                      return(
                      <tr key ={index}>
                        <td>{no}</td>
                        <td>{data.printOptions}</td>
                        <td className='edittd'>
                          <EditFrame data={data} />
                        </td>
                      </tr>
                    )})}
                  </MDBTableBody>
                </MDBTable>
              </MDBCardBody>
            </MDBCard>
          </MDBCol>
        </MDBRow>





      </div>
    );
  }
}

export default inject('startingStore')(observer(CategoryTable));
