import { inject, observer } from 'mobx-react';
import React, { Component } from 'react';
import ArtworkFilter from '../../components/ArtworkFilter';
import Footer from '../../components/Footer';
import Navbar from '../../components/Navbar';

import {MDBBtn, MDBIcon, MDBNavLink} from 'mdbreact';
import {BackTop} from 'antd';
import {Link} from 'react-router-dom';
import {toJS} from 'mobx';

import {Pagination} from 'antd'; //new

import {message} from 'antd';
const numEachPage = 10;
class Artworks extends Component {
  constructor(props) {
    super(props);
    this.state = {
      img: [],
      price: [],
      categories: [],
    };
  }

  componentDidMount() {
    let {
      startingStore: {getArtists, getLatestArts, getCategories,getArtworkInfo},
    } = this.props;
    getLatestArts();
    getArtists();
    getCategories();
    getArtworkInfo()
    this.selectedCheckboxes = new Set();
  }

  setPrice = (amt) => {
  
    let {
      startingStore: {listOfLatestArts},
    } = this.props;
    console.log(this.state.price,listOfLatestArts,'aaa')
    let arts = [];
    let filteredArtwork = listOfLatestArts.filter((art ) => parseInt(art.artPrice) >= parseInt(amt[0]) && parseInt(art.artPrice) <= parseInt(amt[1]) ).map(art =>art)
    console.log(filteredArtwork,'aaaa')
    // this.setState({price: arts});
  };

  render() {
    let mydata = JSON.parse(sessionStorage.getItem('userData'))
    let {
      startingStore: {listOfCategories, filter,artworks, mylists, addMyLists,listOfMyLists,},
    } = this.props;
    let categoryList = listOfCategories.map((cat) => cat.catType);
    let type = filter !== false ? filter : sessionStorage.getItem('type');
    let h3Type = () => {
      if (type === 'discover') {
        return <h3>Shop by price </h3>;
      } else if (type === 'foryou') {
        return <h3>Recommended For You</h3>;
      } else if (type === 'latest') {
        return <h3>Latest Artworks</h3>;
      } else if (type === 'categoriesfollow') {
        return <h3>Categories You Follow</h3>;
      } else if (type === 'artistyoufollow') {
        return <h3>Artist You Follow</h3>;
      } else if (type === 'bestseller') {
        return <h3>Best Sellers</h3>;
      }
    };
  


    
    let addtoList=(rtwrk)=>{

      let getmyList = listOfMyLists.filter( art => art.artworkID === rtwrk.artworkID).length
    
    if (getmyList === 0){
       
       mylists.setProperty('mylistsID',`${rtwrk.artworkID.slice(0,4)}-${Math.floor(1000 + Math.random() * 900)}`)
       mylists.setProperty('accID',mydata.accID)
       mylists.setProperty('artworkID',rtwrk.artworkID)
     
       mylists.setProperty('artName',rtwrk.artName)
       mylists.setProperty('artTheme',rtwrk.artTheme)
       mylists.setProperty('artStyle',rtwrk.artStyle)
       mylists.setProperty('artPrice',rtwrk.artPrice)
       mylists.setProperty('artistID',rtwrk.accID)
       mylists.setProperty('artistName',rtwrk.artistName)
       mylists.setProperty('artworkDateCreated',rtwrk.artworkDateCreated)
       mylists.setProperty('artType',rtwrk.artType)
       mylists.setProperty('artworkImg',rtwrk.artworkImg)
       
       addMyLists()
    
       const success = () => {
        message
          .loading('', 0.5)
          .then(() => message.success('Artwork added to your list', 3));
      };
      setTimeout(() =>{
        success()
      },500)
    }else{
      const success = () => {
        message
          .loading('', 0.5)
          .then(() => message.success('Artwork already on your list', 3));
      };
      setTimeout(() =>{
        success()
      },500)
     
    }
     }
    return (
      <div className='home'>
        <Navbar />
        <div className='maincon con artworks'>
          <div className='aside'>
            <div className='filter'>
              <div className='filter'>
               
                <form>
         

                  <h5>Price</h5>
                  <ul>
                    <li className='custom-control custom-checkbox'>
                      <input
                        type='checkbox'
                        className='custom-control-input'
                        id='anyprice'
                        onChange={() => {
                          this.setPrice(['0', '999999']);
                        }}
                      />
                      <label className='custom-control-label' htmlFor='anyprice'>
                        Any
                      </label>
                    </li>
                    <li className='custom-control custom-checkbox'>
                      <input
                        type='checkbox'
                        className='custom-control-input'
                        id='pricerange1'
                        onChange={() => {
                          this.setPrice(['0', '5000']);
                        }}
                      />
                      <label className='custom-control-label' htmlFor='pricerange1'>
                        PHP0 - PHP5,000
                      </label>
                    </li>
                    <li className='custom-control custom-checkbox'>
                      <input
                        type='checkbox'
                        className='custom-control-input'
                        id='pricerange2'
                        onChange={() => {
                          this.setPrice(['5000', '10000']);
                        }}
                      />
                      <label className='custom-control-label' htmlFor='pricerange2'>
                        PHP5,000 - PHP10,000
                      </label>
                    </li>
                    <li className='custom-control custom-checkbox'>
                      <input
                        type='checkbox'
                        className='custom-control-input'
                        id='pricerange3'
                        onChange={() => {
                          this.setPrice(['10000', '20000']);
                        }}
                      />
                      <label className='custom-control-label' htmlFor='pricerange3'>
                        PHP10,000 - PHP20,000
                      </label>
                    </li>
                    <li className='custom-control custom-checkbox'>
                      <input
                        type='checkbox'
                        className='custom-control-input'
                        id='pricerange4'
                        onChange={() => {
                          this.setPrice(['20000', '30000']);
                        }}
                      />
                      <label className='custom-control-label' htmlFor='pricerange4'>
                        PHP20,000 - PHP30,000
                      </label>
                    </li>
                    <li className='custom-control custom-checkbox'>
                      <input
                        type='checkbox'
                        className='custom-control-input'
                        id='pricerange5'
                        onChange={() => {
                          this.setPrice(['30000', '40000']);
                        }}
                      />
                      <label className='custom-control-label' htmlFor='pricerange5'>
                        PHP30,000 - PHP40,000
                      </label>
                    </li>
                    <li className='custom-control custom-checkbox'>
                      <input
                        type='checkbox'
                        className='custom-control-input'
                        id='pricerange6'
                        onChange={() => {
                          this.setPrice(['40000', '50000']);
                        }}
                      />
                      <label className='custom-control-label' htmlFor='pricerange6'>
                        PHP40,000 - PHP50,000
                      </label>
                    </li>
                    <li className='custom-control custom-checkbox'>
                      <input
                        type='checkbox'
                        className='custom-control-input'
                        id='pricerange7'
                        onChange={() => {
                          this.setPrice(['50000', '1000000']);
                        }}
                      />
                      <label className='custom-control-label' htmlFor='pricerange7'>
                        PHP50,000 and Up
                      </label>
                    </li>
                  </ul>
         
              
                </form>
              </div>
            </div>
          </div>
          <div className='main'>
            <section className='latest'>
              {h3Type()}
              {/* <ArtworkFilter price={this.state.price} type='latest' /> */}

              <div>
        {this.props.price !== undefined ? (
          <ul className='col3img clearfix'>
            {this.props.price.map((image,i) => {
              return (
                <li key={i}>
                 <Link
                      to={{
                        pathname: `/Art/${image.artworkID}/${image.artistName}`}} className='artlink'>
                    <div className='artlabel'>
                      <span className='new'>NEW</span>
                      <span className='hot'>HOT</span>
                      <span className='type'>
                        {image.artType === 'Secondary'
                          ? 'Second Edition'
                          : 'Original'}
                      </span>
                    </div>
               
                      <img src={image.artworkImg} alt='artwork' />
                    </Link>
                 
                  <div className='artistinfo clearfix pad10'>
              
                  <Link
                      to={{
                      pathname: `/Artist/${image.artistName}`,
                      state: {artistName:image.artistName
                      }
                 
                  }}
                >
                     <p>{image.artistName}</p>
                 
                </Link>
            
            
                <MDBBtn
                      className='ifollow'
                      color='transparent'
                      
                      rounded
                      title='Add To My Lists' 
                     onClick={()=>{addtoList(image)}}
                    >
                    
                        <MDBIcon icon='plus' />
                    
                      </MDBBtn>
          </div>
                </li>
              );
            })}
          </ul>
        ) : (
          <ul className='col3img clearfix'>
            {/* {artworks()} */}

          </ul>
        )}

        {/* <List defaultCurrent={1} pageSize={10} dataSource={listOfArtworks}/> */}

        <div>
          {/* <Pagination //new
            defaultCurrent={1}
            defaultPageSize={numEachPage}
            onChange={this.handleChange}
            total={
              this.props.type.length !== 0
                ? this.props.type.length
                : artworks.length
            }
            //
          /> */}
        </div>

        <div className='bttop clearfix'>
          <MDBNavLink to='/Upload' className='btnYellow'>
            Add Yours
          </MDBNavLink>
          <BackTop>
            {' '}
            <a href='#!'>
              Back to Top <MDBIcon icon='caret-right' />{' '}
            </a>{' '}
          </BackTop>
        </div>
      </div>

            </section>
          </div>


        </div>
        <Footer />
      </div>
    );
  }
}

export default inject('startingStore')(observer(Artworks));
